import { Component, OnInit, NgModule } from '@angular/core';
import { Http, Jsonp, URLSearchParams } from '@angular/http'; //2.導入Http服務
import 'rxjs/add/operator/map';

@Component({
  selector: 'app-query-data',
  templateUrl: './query-data.component.html',
  styleUrls: ['./query-data.component.css'],
  providers: [] //注意!!!
})



export class QueryDataComponent implements OnInit {
  data: any;
  anyErrors: any;
  finished: any;
  subData: any = [];
  private img_green: string = require("./image/bullet_green.png");
  private img_red: string = require('./image/bullet_red.png');
  stateImg: string = this.img_red;

  options = ["All"];

  constructor(private http: Http) { } //3.聲明引入

  ngOnInit() {
    this.getData();
  }

  getSubData_url0(key: string, arr: any) {
    this.subData = [];

    for (let item of arr) {
      if ((item["city"] == key) || (key == "All")) {
        this.subData.push(item);
      }
    }

  }
  // getSubData_url1(key: string, arr: any) {
  //   this.subData = [];
  //   debugger;
  //   if (arr.length) {
  //     for (let item of arr) {
  //       if (item["County"] == key) {
  //         this.subData.push(item);
  //       }
  //     }
  //   }
  // }

  onChange(e) {
    let key: string = e.currentTarget.value;
    let subscription = this.data.subscribe(
      value => this.getSubData_url0(key, value),
      error => this.anyErrors = true,
      () => this.finished = true
    );
  }

  getData() {
    let url0 = 'datas/query-data/weather_station.json'; //各縣市空汙數值
    // let url1 = '/Data/REWXQA/?$orderby=SiteName&$skip=0&$top=1000&format=json'; //各縣市空汙數值
    // let url2 = '/Upload/UserFiles/%E8%BB%8A%E7%A8%AE%E5%88%A5%E5%AE%A2%E9%81%8B%E9%87%8F.json'; //台鐵車種運量(key有中文會亂碼)

    this.data = this.http.get(url0).map(x => x.json());

    let subscription = this.data.subscribe(
      value => this.sussGetData(value),
      error => this.stateImg = this.img_red,
      () => this.finished = true
    );
  }

  sussGetData(arr: any) {
    this.getCityList(arr);
    this.subData = arr;
  }

  getCityList(arr: any) {
    this.stateImg = this.img_green;
    for (let item of arr) {
      if (this.options.indexOf(item['city']) == -1) {
        this.options.push(item['city']); //setList Combobox
      }
    }
  }

}



