import { HttpModule, Http } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { TestFormComponent } from './testForm/test-form.component';
import { BubbleSortComponent } from './bubbleSort/bubble-sort.component';
import { RockPaperScissorsComponent } from './rock-paper-scissors/rock-paper-scissors.component';
import { QueryDataComponent } from './query-data/query-data.component';
import { CreditCheckoutComponent } from './credit-checkout/credit-checkout.component';
import { TranslateLoader, TranslateStaticLoader, TranslateModule } from 'ng2-translate';
import { ShoppingCartComponent } from './shopping-cart/shopping-cart.component';
import { RouterModule } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AppRoutes } from './app.routes';
import { DemoCollectionComponent } from './demo-collection/demo-collection.component';
import { D3jsDemoComponent } from './d3js-demo/d3js-demo.component';
import { TodoListComponent } from './todo-list/todo-list.component';

export function httpFactory(http: Http) {
  return  new TranslateStaticLoader(http, 'i18n', '.json');
}
@NgModule({
  declarations: [
    AppComponent,
    TestFormComponent,
    BubbleSortComponent,
    RockPaperScissorsComponent,
    QueryDataComponent,
    CreditCheckoutComponent,
    ShoppingCartComponent,
    DashboardComponent,
    DemoCollectionComponent,
    D3jsDemoComponent,
    TodoListComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule, //1.在ngModule中導入HttpModule    
    TranslateModule.forRoot({
      provide: TranslateLoader,
      useFactory: httpFactory,
      deps: [Http]
    }),
    RouterModule.forRoot(AppRoutes)
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }


