import { ShoppingCartService } from './../shopping-cart.service';
import { ActivatedRoute } from '@angular/router';
// 結合jsonBin做post、get、patch
// 卡號判別發卡單位換圖ok
// 清空畫面ok
// 取出畫面上所有資料ok
// 有效年=now()+20 ok
// 多國語言(繁體、簡體、英文)ok
// 自動判別使用者語系ok
import { Http, Headers } from '@angular/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { TranslateModule, TranslateService } from 'ng2-translate';

@Component({
  selector: 'app-credit-checkout',
  templateUrl: './credit-checkout.component.html',
  styleUrls: ['./credit-checkout.component.css']
})
export class CreditCheckoutComponent implements OnInit {
  form: FormGroup;
  yearList: number[] = [];
  order: JSON;
  orderDetail = this.shoppingCartService.orderDetail;

  constructor(private formBuilder: FormBuilder, private translate: TranslateService, private http: Http, private route: ActivatedRoute, private shoppingCartService: ShoppingCartService) { }

  sussGetData(arr: any) {
    console.log(arr);
  }
  sussPostData(arr: any) {
    console.log(arr);
  }
  sussPatchData(arr: any) {
    console.log(arr);
  }

  getData(url) {
    let headers = new Headers();
    this.createAuthorizationHeader(headers);
    this.http.get(url, {
      headers: headers
    }).map(x => x.json()).subscribe(
      value => this.sussGetData(value),
      error => this.sussGetData(error),
      () => console.log('finished getData'));
  }
  postData(url, data) {
    let headers = new Headers();
    this.createAuthorizationHeader(headers);
    this.http.post(url, data, {
      headers: headers
    }).subscribe(
      value => this.sussPostData(value),
      error => this.sussPostData(error),
      () => console.log('finished postData'));
  }
  patchData(url, data) {
    let headers = new Headers();
    this.createAuthorizationHeader(headers);
    this.http.patch(url, data, {
      headers: headers
    }).subscribe(
      value => this.sussPatchData(value),
      error => this.sussPatchData(error),
      () => console.log('finished patchData'));
  }
  createAuthorizationHeader(headers: Headers) { //添加header
    headers.append('authorization', 'token 646e4ae3-e5f6-4ead-a02f-47ebb7f4f437');
    headers.append("Content-Type", "application/json");
  }

  initComboBox() {
    let yearNow: number = new Date().getFullYear();
    this.yearList.push(yearNow);
    for (var _i = 0; _i < 20; _i++) {
      this.yearList.push(yearNow + _i);
    }
  }
  initLang() {
    // 判斷瀏覽器語言，如果抓不到預設為中文
    let userLang = navigator.language.split('-')[0];
    userLang = /(zh|en|de)/gi.test(userLang) ? userLang : 'en';
    this.translate.setDefaultLang(userLang);
    this.translate.use(userLang);

    //ts中取翻譯的方法
    // this.translate.get('HelloTrans').subscribe((val: string) => {
    //   console.log('HelloTrans', val);
    // });
  }
  initForm() {
    this.form = this.formBuilder.group({
      creditCardType: 0,
      creditCardNumber: '1111222233334444',
      creditCardHolderName: 'Alex,Tzeng',
      creditCardExpirationMonth: '',
      creditCardExpirationYear: '',
      cardSecurityCode: '',
      total: '99.9'
    });
  }

  ngOnInit() {
    this.initComboBox();
    this.initLang();
    this.initForm();
  }
  changeLang(v: string) {
    this.translate.use(v);
  }

  newGuid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }

  onSubmit(ss: any) {
    // console.log(this.form.value);
    this.order = this.form.value;
    this.order['orderID'] = this.newGuid();
    this.order['dateTime'] = new Date().toISOString();
    this.order['orderDetail'] = this.orderDetail;
    console.log(this.order);

    //this.getData('/rosethorn999/volvoOrder');
    //this.postData('/rosethorn999/volvoOrder',data);
    // this.patchData('/rosethorn999/volvoOrder', this.order);
    this.patchData('https://jsonbin.org/rosethorn999/volvoOrder', this.order);
  }
  onClear() {
    this.form.reset();
  }

  verifyType(e: any) {
    let v: string = e.currentTarget.value.toString();
    v = v.substring(0, 1);
    let type: number = +v; //to number
    if (type > 3 || 0 > type) { type = 1; } //預防亂打導致comboBox炸裂
    this.form.controls['creditCardType'].setValue(type);
  }

  stringifyJSON(v: JSON) {
    // return JSON.stringify(this.form.value, null, 2);
    return JSON.stringify(v, null, 2);
  }
}


