import { VolvoCar } from '../volvo-car';
import { ShoppingCartService } from './../shopping-cart.service';
import { ActivatedRoute, RouterModule } from '@angular/router';
import { Http } from '@angular/http';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-shopping-cart',
  templateUrl: './shopping-cart.component.html',
  styleUrls: ['./shopping-cart.component.css']
})
export class ShoppingCartComponent implements OnInit {
  carInfo = [];
  cart = {};

  constructor(private http: Http, private shoppingCartService: ShoppingCartService) { }

  ngOnInit() {
    this.getData();
  }

  getData() {
    let url0 = 'datas/shopping-cart/carInfo.json';

    this.http.get(url0).map(x => x.json()).subscribe(
      value => this.insertCars(value),
      error => console.log(error),
      () => console.log('finish'));
  }

  insertCars(arr: any) {
    let favToArr = function (count: number) {
      let r = [];
      for (var index = 0; index < count; index++) {
        r.push(0);
      }
      return r;
    }

    for (var i = 0; i < arr.length; i++) {
      let item: VolvoCar = new VolvoCar(
        arr[i].type,
        'images/shopping-cart/cars/' + arr[i].image,
        arr[i].model,
        arr[i].price,
        0,
        favToArr(arr[i].favorite)
      );

      this.carInfo.push(item);
    }
  }

  addToCart(model: string, quantity: number) {
    for (var i = 0; i < this.carInfo.length; i++) {
      if (model == this.carInfo[i].model) {
        this.carInfo[i].quantity = quantity;
      };
    }
    console.log(this.carInfo);
  }

  checkOut() {
    this.shoppingCartService.orderDetail = this.carInfo;
  }
  clear() {
    for (var i = 0; i < this.carInfo.length; i++) {
      this.carInfo[i].quantity = 0;
    }
  }
}


