import { environment } from './../../environments/environment.prod';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-rock-paper-scissors',
  templateUrl: './rock-paper-scissors.component.html',
  styleUrls: ['./rock-paper-scissors.component.css']
})

export class RockPaperScissorsComponent implements OnInit {
  public computerImg: string = require('./image/question.jpg');
  public playerImg: string = require('./image/question.jpg');
  public winner: string;
  public log: string = "";

  private img_rock: string = require("./image/rock.png");
  private img_paper: string = require('./image/paper.png');
  private img_scissors: string = require('./image/scissors.png');

  constructor() { }

  ngOnInit() { }

  setIcon(val: number) {
    //0剪刀1石頭2布
    let r: string;
    if (val == 0) {
      r = this.img_scissors;
    } else if (val == 1) {
      r = this.img_rock;
    } else {
      r = this.img_paper;
    }
    return r;
  }

  getWhoWin(c: number, p: number) {
    //0剪刀1石頭2布
    //0平手1電腦贏2玩家贏
    let result: number = 0;
    let resultString: string;

    if (c == p) {
      result = 0
    } else if (p > c) {
      let v = (p - c) % 2;
      result = v == 0 ? 2 : 1;
    } else if (c > p) {
      let v = (c - p) % 2;
      result = v == 0 ? 1 : 2;
    }
    switch (result) {
      case 0:
        resultString = "Try again!";
        break;
      case 1:
        resultString = "Computer Win!";
        break;
      case 2:
        resultString = "Player Win!";
        break;
    }
    return resultString;
  }

  startGame() {
    let computer: number = Math.floor(Math.random() * 3);
    let player: number = Math.floor(Math.random() * 3);

    this.computerImg = this.setIcon(computer);
    this.playerImg = this.setIcon(player);

    this.winner = this.getWhoWin(computer, player);
    this.log += "[" + String(computer) + "," + String(player) + "]" + " " + this.winner;


  }


}



