import { Component, OnInit } from '@angular/core';
import { Insertion } from './Insertion';
import { Bubble_sort_var } from './bubble_sort'
import { Observable } from 'rxjs/Rx';

@Component({
  moduleId: module.id,
  selector: 'app-bubble-sort',
  templateUrl: './bubble-sort.component.html',
  styleUrls: ['./bubble-sort.component.css']
})
export class BubbleSortComponent implements OnInit {
  var_b = new Array(this.getRandom(), this.getRandom(), this.getRandom(), this.getRandom(), this.getRandom(),
    this.getRandom(), this.getRandom(), this.getRandom(), this.getRandom(), this.getRandom());
  bsv = new Bubble_sort_var(this.var_b);
  ition = new Insertion();

  constructor() { }

  ngOnInit() {
    this.ition.b_sort(this.bsv.bubble_view);
  }
  getRandom() {
    let r: number = Math.floor(Math.random() * 99) + 1;
    if (r < 10) { //求美觀讓數字都雙位數
      r += 10;
    }
    return r;
  }
}


